.tui-calendar-layout {
  box-sizing: border-box;
  position: relative;
  white-space: nowrap;
}

.tui-calendar-layout * {
  box-sizing: border-box;
}

.tui-calendar-layout .tui-calendar-panel-resizer {
  user-select: none;
}

.tui-calendar-layout .tui-calendar-panel-resizer:hover {
  border-color: #999;
}

.tui-calendar-layout .tui-calendar-panel-resizer-guide {
  position: absolute;
}

.tui-calendar-layout .tui-calendar-dragging {
  position: absolute;
}

.tui-calendar-layout.tui-calendar-horizontal .tui-calendar-panel,
.tui-calendar-layout.tui-calendar-horizontal .tui-calendar-panel-resizer,
.tui-calendar-layout.tui-calendar-horizontal .tui-calendar-draggable {
  display: inline-block;
  vertical-align: middle;
}

