.tui-calendar-timegrid-times {
  display: inline-block;
  position: relative;
  border-right: 1px solid #e5e5e5;
  font-size: 11px;
}

.tui-calendar-timegrid-times .tui-calendar-timegrid-time {
  height: 52px;
  text-align: right;
  position: relative;
  line-height: 52px;
  top: -26px;
  right: 5px;
  color: #333;
}

.tui-calendar-timegrid-times .tui-calendar-timegrid-time.tui-calendar-timegrid-time-past {
  color: #bbb;
}

.tui-calendar-timegrid-times .tui-calendar-timegrid-time.tui-calendar-timegrid-time-first {
  top: 5px;
  line-height: normal;
}

.tui-calendar-timegrid-times .tui-calendar-timegrid-time.tui-calendar-timegrid-time-last {
  height: 0;
  top: -36px;
}

.tui-calendar-timegrid-times .tui-calendar-timegrid-time-hidden {
  visibility: hidden;
}

.tui-calendar-timegrid-times .tui-calendar-timegrid-current-time {
  position: absolute;
  text-align: right;
  right: 5px;
  color: #515ce6;
}

.tui-calendar-timegrid-column {
  position: relative;
  border-right: 1px solid #e5e5e5;
}

.tui-calendar-timegrid-column .tui-calendar-timegrid-gridline {
  position: relative;
  height: 52px;
  border-bottom: 1px solid #e5e5e5;
}

.tui-calendar-timegrid-column .tui-calendar-timegrid-gridline .tui-calendar-timegrid-gridline-half {
  position: absolute;
  height: 26px;
  border-bottom: 1px dotted #f9f9f9;
}

.tui-calendar-timegrid-column .tui-calendar-event-background {
  position: absolute;
}

.tui-calendar-timegrid-container {
  height: 100%;
  overflow: hidden;
  overflow-y: scroll;
  user-select: none;
}

.tui-calendar-timegrid-sticky-container {
  position: absolute;
  top: 0;
  border-bottom: 1px solid #e5e5e5;
  background-color: white;
  font-size: 11px;
  user-select: none;
}

.tui-calendar-timegrid-sticky-container .tui-calendar-timegrid-timezone-label {
  display: inline-block;
  text-align: right;
  padding-right: 5px;
  border-right: 1px solid #e5e5e5;
  height: 40px;
  line-height: 40px;
}

.tui-calendar-timegrid-sticky-container .tui-calendar-timegrid-collapse-button {
  cursor: pointer;
  position: absolute;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
  top: 2px;
  bottom: 2px;
  width: 10px;
  border-left: none;
}

.tui-calendar-timegrid-sticky-container .tui-calendar-timegrid-collapse-button .tui-calendar-icon {
  width: 5px;
}

.tui-calendar-event-background {
  display: inline-block;
}

.tui-calendar-holiday {
  color: red;
  font-size: 15px;
}
